.login-page {
  height: 100vh;
  background: #12c2e9; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #f64f59,
    #c471ed,
    #12c2e9
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(#f64f59),
    color-stop(#c471ed),
    to(#12c2e9)
  );
  background: -webkit-linear-gradient(
    left,
    #f64f59,
    #c471ed,
    #12c2e9
  );
  background: linear-gradient(
    to right,
    #f64f59,
    #c471ed,
    #12c2e9
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.login-form {
  background: #fff;
  padding: 30px;
  text-align: center;
}

.login-card {
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
  top: 100px;
}

.login-form button {
  width: 100%;
}

.login-form .ant-radio-group-large .ant-radio-button-wrapper {
  margin: 5px;
  min-width: 150px;
}

#components-layout-demo-custom-trigger .trigger {
  font-size: 18px;
  line-height: 64px;
  padding: 0 24px;
  cursor: pointer;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
}

#components-layout-demo-custom-trigger .trigger:hover {
  color: #1890ff;
}

#components-layout-demo-custom-trigger .logo {
  height: 32px;
  background: rgba(255, 255, 255, 0.2);
  margin: 16px;
}

.ant-layout {
  min-height: 100vh;
}
.ant-menu.ant-menu-light {
  min-height: 100%;
}

.ant-layout-header .trigger.anticon {
  font-size: 18px;
  margin-left: 20px;
}
.centered {
  position: absolute;
  top: 45%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-align: center;
}
.centered p {
  margin-bottom: 5px;
}
.table-info span {
  margin: 0 5px;
}
.centered .status {
  text-align: center;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  position: absolute;
  top: 70px;
  left: 0px;
}
.centered .status.danger {
  background: #ff6161;
}
.centered .status.warning {
  background: #ffde4d;
}
.centered .status.success {
  background: #61ff7b;
}
