/* Dashboard */
.dashboard .ant-card {
  border-radius: 10px;
  margin-bottom: 1em;
}
.dashboard .short {
  color: #fff;
}
.statistic {
  margin-top: 2em;
  padding: 1em 2em;
  border-color: #e8e8e8;
  border-radius: 10px;
}
.statistic .title {
  font-size: 20px;
  font-weight: bold;
}

.loading-area {
  width: 100%;
  padding: 2em;
  font-size: 20px;
  text-align: center;
}

.noti-btn {
  position: absolute;
  top: 2em;
  right: 3em;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

